
.fade-enter-active, .fade-leave-active {
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.detail {
  width: 100%;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 3000;
  background: rgba(0, 0, 0, 0.5);
}
.detail .modal {
    width: 84%;
    position: absolute;
    -webkit-border-radius: 3px;
            border-radius: 3px;
    left: 8%;
    right: 8%;
    top: 80px;
    z-index: 20000;
    background: #fff;
    border: 1px solid #999;
}
.detail .modal img {
      border: none;
      outline: none;
      width: 100%;
      max-width: 100%;
      -webkit-border-radius: 3px 3px 0 0;
              border-radius: 3px 3px 0 0;
      max-height: 240px;
}
.detail .modal .right {
      padding: 8px 15px 20px;
}
.detail .modal .right .title {
        font-weight: bold;
        color: #222;
        font-size: 15px;
        padding-bottom: 2px;
}
.detail .modal .right .count {
        font-size: 12px;
        color: #666;
}
.detail .modal .right .price {
        color: #FF6600;
        font-size: 18px;
        font-weight: bold;
        padding-top: 10px;
        overflow: hidden;
}
.detail .modal .right .price i {
          font-size: 13px;
          font-style: normal;
}
.detail .modal .right .price > span {
          float: left;
}



