
.bounce-enter-active {
  -webkit-animation: bounce-in 0.1s;
          animation: bounce-in 0.1s;
}
@-webkit-keyframes bounce-in {
from {
    -webkit-transform: scale3d(0, 0, 0);
            transform: scale3d(0, 0, 0);
}
to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}
@keyframes bounce-in {
from {
    -webkit-transform: scale3d(0, 0, 0);
            transform: scale3d(0, 0, 0);
}
to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}
.dialog .modal {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 2333;
  background: rgba(0, 0, 0, 0.5);
}
.dialog .modal span {
    font-size: 35px;
    color: #fff;
    position: absolute;
    left: 48%;
    bottom: 10%;
    font-weight: normal;
}
.dialog .con {
  width: 90%;
  margin: auto;
  -webkit-border-radius: 5px;
          border-radius: 5px;
  background: #fff;
  -webkit-filter: blur(0.3px);
          filter: blur(0.3px);
  padding: 20px 20px 25px;
  position: absolute;
  z-index: 44444;
  top: 150px;
  left: 5%;
  right: 5%;
}
.dialog .con .title {
    font-size: 20px;
    text-align: center;
    color: #333;
}
.dialog .con .total {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-justify-content: space-around;
        -ms-flex-pack: distribute;
            justify-content: space-around;
    color: #333;
    margin-top: 15px;
}
.dialog .con .item {
    text-align: center;
}
.dialog .con .item h3 {
      font-size: 15px;
}
.dialog .con .item span {
      font-size: 12px;
      color: #888;
}
.dialog .con .notice {
    margin-top: 18px;
    text-align: center;
}
.dialog .con .notice h2 {
      font-size: 14px;
      color: #888;
      font-weight: normal;
      padding-bottom: 8px;
}
.dialog .con .notice p {
      font-size: 14px;
      color: #555;
}



